import {useEffect} from "react"
import {setSlideInfo,setScrolInfo} from "./features/appSlice";
import {useDispatch} from "react-redux"
import Captain from "./vectors/ico/captn.svg"
import Iit from "./vectors/ico/iit.svg"
import Blog from "./vectors/ico/blogico.svg"
import Store from "./vectors/ico/store.svg"
import Activities from "./vectors/ico/acitivity.svg"
import { ChevronDownIcon } from "@heroicons/react/outline";
import { motion, useViewportScroll,useTransform  } from "framer-motion";
function SlideBar() {
    const { scrollYProgress } = useViewportScroll();
    const DataTable = [
        {
            title: "Green",
            subtitle: "IIT GN",
            content: "The Green Club is IIT Gandhinagar’s sustainability club which aims to promote cleanliness and sensitivity to environmental issues. IIT Gandhinagar has one of the most efficient campuses in terms of green development and eco-friendly initiatives, including the promotion of aesthetic value and preservation of the campus’s biodiversity. The Green Club facilitates this further, by encouraging the student community to also work towards making the campus sustainable and develop innovative ideas for conserving the environment.",
            logo: Iit,
        },
        {
            title: "Captain",
            subtitle: "Kachra",
            content: "Captain Kachra is a game designed to increase awareness of waste segregation as an initiative by the Green Office. The game teaches what various products go into which bin in a fun mini-game.",
            logo: Captain,
        },
        {
            title: "Campus",
            subtitle: "Activities",
            content: "This game is aimed to increase awareness of waste segregation as an initiative by the Green Office. The game is structured as such when the game starts, daily wastage products pop up on the screen and the participant is required to find the correct bin for the disposal of the product",
            logo: Activities,
        },
        {
            title: "Green",
            subtitle: "Blog",
            content: "This initiative began with a survey among the female students of IIT Gandhinagar, the majority of whom reported using plastic-based period products. Menstruating members who haven't taken the survey are encouraged to do so. This survey helps you understand why plastic-based menstrual products are a huge issue and introduce you to the many sustainable options in the market.",
            logo: Blog,
        },
        {
            title: "Store",
            subtitle: "Green",
            content: "The Swachhta Pakhwada launched by Government of India, is a fortnight-long program observed to ensure mass participation of citizens in Swachhta activities and to truly transform Swachh Bharat into a citizen’s movement.",
            logo: Store,
        }
    ]
    const n= parseInt(DataTable.length) ;
    const scale = useTransform(scrollYProgress, [0, 1], [-152, -25]);
    const scale2 = useTransform(scrollYProgress, [0, 1], [0, n-1]);
    
    const dispatch = useDispatch();
    
    useEffect(() => {
        scale2.onChange(()=>{
            fetchData(parseInt(scale2.get()));
            dispatch(setScrolInfo({scroll:scale2.get()}));
        })
        
    }, [])// eslint-disable-line react-hooks/exhaustive-deps
    useEffect(() => {
        fetchData(0);  
    }, [])
    const fetchData = (e)=>{
    dispatch(
       setSlideInfo({
           slideTitle : DataTable[e].title,
           slideSubTitle : DataTable[e].subtitle,
           slideContent : DataTable[e].content,
           logo: DataTable[e].logo,
       }))
    }
    return (
        <div>
            
        <div className=" fixed flex flex-col z-20 top-32 right-6 items-center">
            
        <div className=" px-1 flex flex-col bg-white bg-opacity-10 border-0 rounded-3xl   opacity-70">
            <div onClick={(e)=>window.scrollTo(0, 0.01*window.innerHeight)} className="bg-gray-300 bg-opacity-25 cursor-pointer hover:bg-gray-100 rounded-full w-px p-2 mx-auto my-2"></div>
            <div onClick={(e)=>window.scrollTo(0, window.innerHeight*1.3/5)} className="bg-gray-300 bg-opacity-25 cursor-pointer hover:bg-gray-100 rounded-full w-px p-2 mx-auto my-2"></div>
            <div onClick={(e)=>window.scrollTo(0, window.innerHeight*2.55/5)} className="bg-gray-300 bg-opacity-25 cursor-pointer hover:bg-gray-100 rounded-full w-px p-2 mx-auto my-2"></div>
            <div onClick={(e)=>window.scrollTo(0, window.innerHeight*3.85/5)} className="bg-gray-300 bg-opacity-25 cursor-pointer hover:bg-gray-100 rounded-full w-px p-2 mx-auto my-2"></div>
            <div onClick={(e)=>window.scrollTo(0, window.innerHeight*5/5)} className="bg-gray-300 bg-opacity-25 cursor-pointer hover:bg-gray-100 rounded-full w-px p-2 mx-auto my-2"></div>
        </div>
        <motion.div className="z-21 text-white rounded-full bg-white w-4 h-4 "
                style={{
                    translateY: scale
                  }}></motion.div>
            <div className="w-4 -mt-2 text-white animate-bounce"><ChevronDownIcon/></div>
        </div>
        </div>
    )
}

export default SlideBar
